
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Scroll from 'assembly/scroll/scroll'
import { mapActions } from 'vuex'
import localStore from 'util/localStore'

export default {
components: {
XHeader,
Scroll,
Popup,
CheckIcon,
XDialog,
XTextarea
},
data () {
return {
orderDetail: {},
accountDetail: {},
payPop: false,
payResultPop: false,
selMenu: [{
label: '',
type: 'warn'
}],
payResult: false,
orderNo: '', // 
countDownTime: 0, // 
groupFlag: false,
totalNum: 0,
balance: 0,
creditAvailable: 0,
needGiftProduct: localStore.get('TUP_cart').needGiftProduct,
giftProducts: [],
payLoading: false, // 
showLoading: false,
ifKit: false,
offShelve: [], // 
ifNotProduct: false, // 
ifNotZeng: false, // 
NotProductList: [], // 
kitProduct: [], // kit
inited: false, // 
timer: null 
}
},
methods: {
returnBack () {
this.$uweb.trackEvent('107', '-', '0')
this.$router.go(-1)
},
goBack () {
this.$router.go(-1)
},
formatePrice (num) {
return num / 100
},
closePop () {
this.ifKit = false
},
showKit (value) {
this.ifKit = true
this.kitProduct = value
},
cancelPay () {
this.ifNotProduct = false
this.$router.go(-1)
},
cancelOrder () {
if (this.inited) {
return 
}
this.inited = true
this.$tupHttp(this, 'front/shoppingCart/cancelTemporaryOrder?orderNo=' + this.orderNo, 'POST').then(res => {
this.$vux.toast.text('')
clearInterval(this.timer)
this.ifNotZeng = false
this.$router.go(-1)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
confirmToPay () {
if (this.inited) {
return 
}
this.inited = true
this.$tupHttp(this, 'front/shoppingCart/confirmTemporaryOrder?orderNo=' + this.orderNo, 'POST').then(res => {
this._getInfo()
this.clearCart() 
this.$vux.toast.text('')
localStore.set('TUP_pFirstOrderFlag', true) 
clearInterval(this.timer)
this.ifNotZeng = false
this.payPop = true
this._getClientGiftProducts()
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
toPay () {
this._submit()
},
confirmPay () {
if (this.inited) {
return 
}
this.inited = true
this.payLoading = true
setTimeout(() => {
this.$tupHttp(this, 'front/shoppingCart/pay', 'POST', {
orderNo: this.orderNo
}).then(res => {
this.payPop = false
this.payResult = res.data.valid
this.payResultPop = true
this.payLoading = false
}, () => {
this.inited = false
this.payLoading = false
this.$router.replace({
path: '/orderDetail',
query: {
orderNo: this.orderNo,
redirect: 'shopping'
}
})
}).catch(() => {
this.inited = false
this.payLoading = true
this.$router.replace({
path: '/orderDetail',
query: {
orderNo: this.orderNo,
redirect: 'shopping'
}
})
})
}, 1500)
},
closePayPop () {
if (this.balance + this.creditAvailable < this.orderDetail.accountDetail.balance) {
this.$uweb.trackEvent('111', '-', '0')
} else {
this.$uweb.trackEvent('109', '-', '0')
}
this.payPop = false
this.$router.push({
path: '/orderDetail',
query: {
orderNo: this.orderNo,
redirect: 'shopping'
}
})
},
toOrderDetail () {
this.$router.push({
path: '/orderDetail',
query: {
orderNo: this.orderNo,
redirect: 'shopping'
}
})
},
toRecharge () {
this.$router.push({
path: '/recharge',
query: {
redirect: 'shopping',
amount: Math.round((this.creditAvailable > 1 ? this.orderDetail.accountDetail.balance - this.balance - this.creditAvailable : this.orderDetail.accountDetail.balance - this.balance) * 100) / 100,
orderNo: this.orderNo
}
})
},
toShopping () {
this.$router.push({
path: '/shopping'
})
},
toMine () {
this.$router.push({
path: '/mine',
redirect: 'shopping'
})
},
countDown (time) {
let m, s
m = '0' + Math.floor(time / 60)
s = Math.floor(time - m * 60)
this.timer = setInterval(() => {
s--
s < 10 && (s = '0' + s)
if (s.length >= 3) {
s = 59
m = '0' + (Number(m) - 1)
}
if (m.length >= 3) {
m = '00'
s = '00'
clearInterval(this.timer)
this.$vux.toast.text('~')
this.$router.go(-1)
}
this.countDownTime = m + '' + s + ''
}, 1000)
},
/***
 /front/shoppingCart/submit
1.1 success = true & valid = true
1.2 success = true & valid = false
extra: {'orderNo': B-TS2xxxxxx, 'countDownTime': 120}
modelsSolrProductVo
1.3 success = false & resultCode = 1410
modelsSolrProductVo
1.4 success = false & resultCode = 1411
1.5 success = false & resultCode
***/
_submit (flag) {
if (this.inited) {
return 
}
this.inited = true
let data = this.orderDetail
data.groupFlag = this.groupFlag ? 1 : 0

















if (flag) {
data.isDoubleConfirm = 1
}
let randomNum = Math.floor(Math.random() * 7) 
this.showLoading = true
setTimeout(() => {
this.$tupHttp(this, 'front/shoppingCart/submit', 'POST', data).then(res => {
if (res.data.success && res.data.valid) { // 
this.orderNo = res.data.extra.orderNo
this._getInfo()
this.clearCart() 
this.$vux.toast.text('')
localStore.set('TUP_pFirstOrderFlag', true) 
this.payPop = true
this._getClientGiftProducts()
} else if (res.data.success && !res.data.valid) { // 
this.NotProductList = res.data.models.filter(item => { // 
return item.bizErrorMsg
})
this.countDown(res.data.extra.countDownTime)
this.orderNo = res.data.extra.orderNo
this.ifNotZeng = true
} else if (!res.data.success && (res.data.resultCode === '1420' || res.data.resultCode === '1421')) { // 
this.$vux.toast.text('~')
this.$router.go(-1)
} else if (!res.data.success && res.data.resultCode === '1415') { // 
this.offShelve = res.data.extra

let storageCart = localStore.get('TUP_cart')
storageCart.product = storageCart.product.map(i => {
for (let j = 0; j < this.offShelve.length; j++) {
if (i.pCode === this.offShelve[j]) {
i.pStatus = 2
if (!i.mqq) { // 
i.selected = false
}
}
}
return i
})
storageCart.promotions = storageCart.promotion.map(i => {
for (let j = 0; j < i.promotionRuleVO.productList.length; j++) {
for (let k = 0; k < this.offShelve.length; k++) {
if (i.promotionRuleVO.productList[j].pCode === this.offShelve[k]) {
i.promotionRuleVO.productList[j].pStatus = 2
if (!i.promotionRuleVO.productList[j].mqq) {
i.promotionRuleVO.productList[j].selected = false
}
}
}
}
if (i.type !== 0) {
for (let l = 0; l < i.promotionRuleVO.rules.length; l++) {
if (i.promotionRuleVO.rules[l].chose === 1 || i.promotionRuleVO.rules[l].chose === 3) {
for (let m = 0; m < i.promotionRuleVO.rules[l].giftList.length; m++) {
for (let n = 0; n < this.offShelve.length; n++) {
if (i.promotionRuleVO.rules[l].giftList[m].pCode === this.offShelve[n]) {
i.promotionRuleVO.rules[l].giftList[m].pStatus = 2
if (!i.promotionRuleVO.rules[l].giftList[m].mqq) {
i.promotionRuleVO.rules[l].giftList[m].selected = false
}
}
}
}
}
}
}
return i
})
this.saveCart({
type: 3,
item: {
product: storageCart.product,
promotion: storageCart.promotion
}
})
this.$vux.toast.text(res.data.message)
this.$router.go(-1)
} else if (!res.data.success && res.data.resultCode === '1410') { // 
this.NotProductList = res.data.models.filter(item => {

return item.bizErrorMsg && item.promotionPrdType !== 3 && item.promotionPrdType !== 4
})

let storageCart = localStore.get('TUP_cart')
storageCart.product = storageCart.product.map(i => {
for (let j = 0; j < this.NotProductList.length; j++) {
if (i.pCode === this.NotProductList[j].pCode && !this.NotProductList[j].mustQty && this.NotProductList[j].availableQty) {
i.shopCartNum = Math.min(this.NotProductList[j].shopCartNum, this.NotProductList[j].availableQty)
i.availableQty = this.NotProductList[j].availableQty
}
}
return i
})
storageCart.promotions = storageCart.promotion.map(i => {
for (let j = 0; j < i.promotionRuleVO.productList.length; j++) {
for (let k = 0; k < this.NotProductList.length; k++) {
if (i.promotionRuleVO.productList[j].pCode === this.NotProductList[k].pCode) {
if (i.type !== 4 && !this.NotProductList[k].mustQty && this.NotProductList[k].availableQty) {
i.promotionRuleVO.productList[j].shopCartNum = Math.min(this.NotProductList[k].shopCartNum, this.NotProductList[k].availableQty)
i.promotionRuleVO.productList[j].availableQty = this.NotProductList[k].availableQty
} else if (i.type === 4 && this.NotProductList[k].avaliableWithRatioQty) {
i.promotionRuleVO.productList[j].availableQty = this.NotProductList[k].avaliableWithRatioQty
i.promotionRuleVO.productList[j].avaliableWithRatioQty = this.NotProductList[k].avaliableWithRatioQty
if (i.promotionRuleVO.productList[j].shopCartNum > i.promotionRuleVO.productList[j].availableQty) {

i.isNotmm = true
}
}
}
}
}
return i
})
this.saveCart({
type: 3,
item: {
product: storageCart.product,
promotion: storageCart.promotion
}
})
this.ifNotProduct = true
} else if (!res.data.success && res.data.resultCode === '1411') { // 
this.$vux.toast.text('~')
} else if (!res.data.success && res.data.resultCode === '1422') { // isDoubleConfirm=1
let thiz = this
this.$vux.confirm.show({
content: '',
confirmText: '',
cancelText: '',
onCancel () {
thiz.$router.go(-1)
},
onConfirm () {
thiz._submit(true)
}
})
} else if (!res.data.success && res.data.resultCode === '1102') { // 
this.inited = false
this._submit()
return
}
this.showLoading = false
this.inited = false
}, () => {
this.showLoading = false
this.inited = false
}).catch(() => {
this.showLoading = false
this.inited = false
})
}, randomNum * 1000)
},
_getInfo () {
this.$tupHttp(this, 'front/user/info/v1').then(res => {
this.balance = this.formatePrice(res.data.model.userAccount.balanceAvailable)
this.creditAvailable = this.formatePrice(res.data.model.userAccount.creditAvailable)
})
},
_getClientGiftProducts () {
this.$tupHttp(this, 'front/shoppingCart/getClientGiftProducts').then(rs => { // 
localStore.set('TUP_cart', {
'promotion': [],
'product': [],
'giftProducts': rs.data.models || [],
'needGiftProduct': 1
})
this._selectMustPromotionsOrProducts() 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_selectMustPromotionsOrProducts () { // 
this.$tupHttp(this, 'front/product/selectMustPromotionsOrProducts').then(res => {
let data = res.data.model
if (data) {
let cart = localStore.get('TUP_cart')
data.promotionItemVOS = data.promotionItemVOS.map(item => {
item.promotionRuleVO.productList.map(childItem => {
if (childItem.isHave) {
childItem.selected = true
}
if (childItem.mustQty) {
childItem.selected = true
childItem.shopCartNum = childItem.mustQty
}
return childItem
})
item.shopCartNum = 1
item.ifEdit = false
item.keyProductPrice = 0
item.promotionTotalPrice = 0
return item
})
cart.promotion = cart.promotion.concat(data.promotionItemVOS)
if (localStore.get('TUP_pFirstorderDate') !== null) { // 
data.solrProductVos = data.solrProductVos.map(item => {
item.shopCartNum = item.mustQty
item.selected = true
return item
})
cart.product = cart.product.concat(data.solrProductVos)
}
this.saveCart({
type: 3,
item: {
product: cart.product,
promotion: cart.promotion
},
isManual: 0
})
}
this._getFullDiscount() 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getFullDiscount () {
this.$tupHttp(this, 'front/product/selectPromotionsByClientIdFromSolr?subType=0').then(res => {
let data = res.data.models
for (let i = 0, len = data.length; i < len; i++) {
this.setPromotionCart({
item: data[i],
isManual: 0
})
}
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_paymentPreview () {
let storageCart = localStore.get('TUP_cart')
let promotions = storageCart.promotion.map((item, index) => { // 
item.promotionRuleVo = item.promotionRuleVO
delete item.promotionRuleVO
item.promotionRuleVo.productList = item.promotionRuleVo.productList.filter(i => {
return i.selected && i.shopCartNum
})
let giftList = []
if (item.promotionRuleVo.rules) {
item.promotionRuleVo.rules.forEach(i => {
if (i.active && (i.chose === 1 || i.chose === 3)) {
i.giftList.forEach(j => {
if (j.selected && j.shopCartNum) {
giftList.push(j)
}
})
}
})
}
item.promotionRuleVo.giftList = giftList
return item
})
promotions = promotions.filter(item => { // 
return item.promotionRuleVo.productList.length || item.promotionRuleVo.giftList.length
})
let products = storageCart.product.filter(item => { // 
return item.selected && item.shopCartNum
})

for (let i = 0; i < promotions.length; i++) {
for (let j = 0; j < promotions[i].promotionRuleVo.productList.length; j++) {
this.totalNum += promotions[i].promotionRuleVo.productList[j].shopCartNum
}
for (let j = 0; j < promotions[i].promotionRuleVo.giftList.length; j++) {
this.totalNum += promotions[i].promotionRuleVo.giftList[j].shopCartNum
}
}
for (let i = 0; i < products.length; i++) {
this.totalNum += products[i].shopCartNum
}
let data = {
products: products || [],
promotions: promotions || [],
isFirstBook: localStore.get('TUP_pFirstorderDate') === null ? 1 : 0,
needGiftProduct: this.needGiftProduct
}
this.$tupHttp(this, 'front/shoppingCart/paymentPreview', 'POST', data).then(res => {
if (res.data.model) {
this.giftProducts = res.data.model.giftProducts || []
if (res.data.model.updates.length || res.data.model.deletes.length) { // 
this.$vux.toast.text('~')
setTimeout(() => {
this.$router.go(-1)
}, 2000)
return
}
}
if (res.data.success && res.data.valid) {
this.orderDetail = res.data.model
} else if (!res.data.success && res.data.resultCode === '1415') { // 
this.orderDetail = res.data.model
this.offShelve = res.data.extra

let storageCart = localStore.get('TUP_cart')
storageCart.product = storageCart.product.map(i => {
for (let j = 0; j < this.offShelve.length; j++) {
if (i.pCode === this.offShelve[j]) {
i.pStatus = 2
if (!i.mqq) { // 
i.selected = false
}
}
}
return i
})
storageCart.promotions = storageCart.promotion.map(i => {
for (let j = 0; j < i.promotionRuleVO.productList.length; j++) {
for (let k = 0; k < this.offShelve.length; k++) {
if (i.promotionRuleVO.productList[j].pCode === this.offShelve[k]) {
i.promotionRuleVO.productList[j].pStatus = 2
if (!i.promotionRuleVO.productList[j].mqq) {
i.promotionRuleVO.productList[j].selected = false
}
}
}
}
if (i.type !== 0) {
for (let l = 0; l < i.promotionRuleVO.rules.length; l++) {
if (i.promotionRuleVO.rules[l].chose === 1 || i.promotionRuleVO.rules[l].chose === 3) {
for (let m = 0; m < i.promotionRuleVO.rules[l].giftList.length; m++) {
for (let n = 0; n < this.offShelve.length; n++) {
if (i.promotionRuleVO.rules[l].giftList[m].pCode === this.offShelve[n]) {
i.promotionRuleVO.rules[l].giftList[m].pStatus = 2
if (!i.promotionRuleVO.rules[l].giftList[m].mqq) {
i.promotionRuleVO.rules[l].giftList[m].selected = false
}
}
}
}
}
}
}
return i
})
this.saveCart({
type: 3,
item: {
product: storageCart.product,
promotion: storageCart.promotion
}
})
this.$vux.toast.text(res.data.message)
} else if (!res.data.success && res.data.resultCode === '1102') {
this.$vux.toast.text(res.data.message)
setTimeout(() => {
this.$router.go(-1)
}, 2000)
}
})
},
...mapActions([
'saveCart',
'setPromotionCart',
'clearCart'
])
},
created () {
this._paymentPreview()
},
beforeRouteEnter (to, from, next) { // 
if (from.name !== 'shoppingCart') {
next(vm => {
vm.$router.push({
path: '/shoppingCart'
})
})
return
}
next()
},
beforeRouteLeave (to, from, next) {
if (this.ifNotZeng) {
this.$tupHttp(this, 'front/shoppingCart/getTemporaryOrder').then(res => {
if (res.data.extra && res.data.extra.orderNo) {
this.$vux.toast.text('~')
} else {
clearInterval(this.timer)
next()
}
})
} else {
next()
}
}
}



